:root {
  // ### Primary

  --nutmeg: hsl(14, 45%, 36%);
  --dark-raspberry: hsl(332, 51%, 32%);

  // ### Neutral

  --white: hsl(0, 0%, 100%);
  --rose-white: hsl(330, 100%, 98%);
  --ggshell: hsl(30, 54%, 90%);
  --light-grey: hsl(30, 18%, 87%);
  --wenge-brown: hsl(30, 10%, 34%);
  --dark-charcoal: hsl(24, 5%, 18%);

  // personal variables
  --background: var(--ggshell);
  --text-color: var(--dark-charcoal);
}
