html {
  font-size: 100%;
  box-sizing: border-box;
  background-color: var(--background);
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

body {
  margin: 0;
  padding: 0;
  font-family: var(--font-outfit);
  background: var(--white);
  color: var(--text-color);
}
@media (min-width: 40em) {
  body {
    margin: 4.6875rem auto 7.6875rem;
    max-width: 45.9375rem;
    border-radius: 1.375rem;
  }
}

.visually-hidden {
  position: absolute;
  left: -10000px;
  top: auto;
  width: 1px;
  height: 1px;
  overflow: hidden;
}

:root {
  --nutmeg: hsl(14, 45%, 36%);
  --dark-raspberry: hsl(332, 51%, 32%);
  --white: hsl(0, 0%, 100%);
  --rose-white: hsl(330, 100%, 98%);
  --ggshell: hsl(30, 54%, 90%);
  --light-grey: hsl(30, 18%, 87%);
  --wenge-brown: hsl(30, 10%, 34%);
  --dark-charcoal: hsl(24, 5%, 18%);
  --background: var(--ggshell);
  --text-color: var(--dark-charcoal);
}

:root {
  --font-young: "Young Serif", serif;
  --font-outfit: "Outfit", sans-serif;
}

.container {
  padding: 0 2rem;
  margin: 0;
}
@media (min-width: 40em) {
  .container {
    padding: 0 2.4375rem;
  }
}

h1,
h2,
h3 {
  margin-top: 0;
  line-height: 1.1;
}

a,
a:visited,
a:active {
  text-decoration: none;
}

.banner {
  margin-bottom: 2.1875rem;
}
.banner img {
  margin-bottom: 2.625rem;
  max-width: 100%;
  padding: 0;
}
@media (min-width: 40em) {
  .banner img {
    margin: 2.4375rem;
    max-width: calc(100% - 4.875rem);
    border-radius: 1rem;
  }
}
.banner__content h1 {
  font-family: var(--font-young);
  font-size: 2.1875rem;
  font-weight: 400;
  margin-bottom: 2.1875rem;
}
@media (min-width: 40em) {
  .banner__content h1 {
    font-size: 2.5rem;
  }
}
.banner__content p {
  line-height: 1.5625rem;
}
.banner__preparation {
  border-radius: 1rem;
  background: var(--rose-white);
  padding: 1.5625rem 1rem;
}
@media (min-width: 40em) {
  .banner__preparation {
    padding: 1.875rem;
  }
}
.banner__preparation h3 {
  color: var(--dark-raspberry);
}
.banner__preparation li {
  line-height: 2;
}
.banner__preparation li span {
  font-weight: 700;
  color: var(--wenge-brown);
}

.recipe__destaque {
  color: var(--wenge-brown);
  border-bottom: 2px solid var(--light-grey);
  padding-bottom: 2rem;
}
.recipe__destaque h3 {
  color: var(--nutmeg);
  font-family: var(--font-young);
  font-weight: 400;
  letter-spacing: 1.1px;
  font-size: 24px;
}
.recipe__destaque ul {
  margin: 0;
  padding-left: 1.5rem;
}
.recipe__destaque li {
  line-height: 2rem;
  padding-left: 0.5rem;
}
@media (min-width: 40em) {
  .recipe__destaque li {
    padding-left: 1rem;
  }
}